@import "init";

.page-header {
  position:fixed;
  width:100%;
  z-index: $header_parallax_bg_zindex;

  .menu, .menu li {
    list-style: none;
    margin:0;
    padding:0;
  }
  .menu li, .menu a {
    display: inline-block;
  }
}

// About
.profile.compact {
  width: 220px;
  padding: 10px 10px;
  position: relative;

  .field--type-text,
  .field--type-link {
    padding-bottom:10px;
  }

  .field--name-user-picture {
    &:before {
      display: block;
      position: absolute;
    }
  }

  .field--name-field-full-name {
    position: absolute;
    top: 97px;
    text-align: center;
    width: 220px;
  }

  .field--name-field-job-title {
    position: absolute;
    top: 138px;
    padding: 0;
    width: 220px;
  }
}

.swipe-indicator {
  display: block;
  padding-top: 10px;
  clear: both;
  margin: 0 auto;
}

.cycle-pager {
  img {
    &:last-of-type {
      margin-right: 0 !important;
    }
  }
}

#block-views-block-portfolio-block-1 {
  .pager {
    margin-bottom: 0;
    ul.pager__items {
      padding-left: 0px;
    }
    .pager__item--next, .pager__item--first,
    .pager__item--previous, .pager__item--last {
      padding:10px 0 0 0;
      margin:0;
    }
    .pager-current, .pager__item--ellipsis {
      display: none;
    }
  }
  .view-content {
    clear:left;
  }
}

input,
textarea,
.form-item-mail {
  padding: 0;
}

#drupal-modal {
  article {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    position: relative;
  }
  .node--type-portfolio {
    h2 {
      height:28px;
      overflow:hidden;
    }
    .field.field--name-title {
      float: left;
    }
  }
}

blockquote {
  margin: 0;
  padding: 10px;
  p {
    padding: 0;
    margin: 0;
  }
}

.contact-message-feedback-form {
  .form-actions {
    position: relative;
  }
  .ajax-progress {
    position: absolute;
    top:5px;
    right:3em;
  }
}

.view-portfolio {
  .pager-last, .pager-previous {
    .ajax-progress {
      transform: scaleX(-1);
    }
  }
}
