/**
 * Variables to be used elsewhere.
 */
.page-header {
  position: fixed;
  width: 100%;
  z-index: 50; }
  .page-header .menu, .page-header .menu li {
    list-style: none;
    margin: 0;
    padding: 0; }
  .page-header .menu li, .page-header .menu a {
    display: inline-block; }

.profile.compact {
  width: 220px;
  padding: 10px 10px;
  position: relative; }
  .profile.compact .field--type-text,
  .profile.compact .field--type-link {
    padding-bottom: 10px; }
  .profile.compact .field--name-user-picture:before {
    display: block;
    position: absolute; }
  .profile.compact .field--name-field-full-name {
    position: absolute;
    top: 97px;
    text-align: center;
    width: 220px; }
  .profile.compact .field--name-field-job-title {
    position: absolute;
    top: 138px;
    padding: 0;
    width: 220px; }

.swipe-indicator {
  display: block;
  padding-top: 10px;
  clear: both;
  margin: 0 auto; }

.cycle-pager img:last-of-type {
  margin-right: 0 !important; }

#block-views-block-portfolio-block-1 .pager {
  margin-bottom: 0; }
  #block-views-block-portfolio-block-1 .pager ul.pager__items {
    padding-left: 0px; }
  #block-views-block-portfolio-block-1 .pager .pager__item--next, #block-views-block-portfolio-block-1 .pager .pager__item--first,
  #block-views-block-portfolio-block-1 .pager .pager__item--previous, #block-views-block-portfolio-block-1 .pager .pager__item--last {
    padding: 10px 0 0 0;
    margin: 0; }
  #block-views-block-portfolio-block-1 .pager .pager-current, #block-views-block-portfolio-block-1 .pager .pager__item--ellipsis {
    display: none; }

#block-views-block-portfolio-block-1 .view-content {
  clear: left; }

input,
textarea,
.form-item-mail {
  padding: 0; }

#drupal-modal article {
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  position: relative; }

#drupal-modal .node--type-portfolio h2 {
  height: 28px;
  overflow: hidden; }

#drupal-modal .node--type-portfolio .field.field--name-title {
  float: left; }

blockquote {
  margin: 0;
  padding: 10px; }
  blockquote p {
    padding: 0;
    margin: 0; }

.contact-message-feedback-form .form-actions {
  position: relative; }

.contact-message-feedback-form .ajax-progress {
  position: absolute;
  top: 5px;
  right: 3em; }

.view-portfolio .pager-last .ajax-progress, .view-portfolio .pager-previous .ajax-progress {
  transform: scaleX(-1); }
